.wrapper {
    margin: 1rem auto;
    padding: 1rem;
    background-color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 30px;
    text-align: center;
    color: 000000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40vw;

    &>* {
        margin: 0.1rem;
        padding: 0.1rem;
    }
}



.title {
    font-weight: 600;
    font-size: 1.5rem;
}